

import Link from "next/link";

const SliderBoxes = [
    {
        id: "1",
        class: "bg-men",
        title: "Shop in Men's Clothing",
        line: "₹ 10.95/- onwards",
        link: "men's clothing"
    }, {
        id: "2",
        class: "bg-jewelery",
        title: "Biggest Sale on Jeweleries",
        line: "Up To 50% Off",
        link: "jewelery"
    }, {
        id: "3",
        class: "bg-women",
        title: "Shop in Women's Clothing",
        line: "₹ 20.95/- onwards",
        link: "women's clothing"
    }, {
        id: "4",
        class: "bg-electronics",
        title: "Grab Your Devices Now",
        line: "From ₹ 50.95/- only",
        link: "electronics"
    }
]

export default function Slider() {
    return (
        <div className=" h-[50vh] w-screen mt-5 bg-white overflow-hidden ">
            <div className="animate-scroll-x h-full flex w-[400vw] scroll-smooth">

            {SliderBoxes.map((box) => (
                <div key={box.id} className={`relative ${box.class} h-full w-screen flex-shrink-0 snap-center`}>
                    <span className="absolute left-1/2 top-1/2 -translate-1/2 font-bold text-xl flex flex-col items-center gap-2">
                        <p>{box.title}</p>
                        <p>{box.line}</p>
                        <Link href={`/category/${box.link}`}><button className="py-2 px-15 bg-red-900 text-white rounded-md text-lg mt-10 cursor-pointer">Shop Now</button></Link>
                    </span>
                </div>
            ))}
            </div>
        </div>
)
}